<template>
  <div class="set">
    <section id="header">
      <HeaderPublc></HeaderPublc>
    </section>
    <div id="cent">
      <cent></cent>
    </div>
    <section id="footer"> </section>
  </div>
</template>
<style scoped>
  .set{height:100%;background:#fff; *zoom: 1;background-color: #f7f7f7;}
  body{background-color:#f7f7f7}
  #cent{margin-left: 240px;margin-right: 20px;background-color: #f7f7f7;}
  #header{position: fixed;width: 220px;top: 0px;left: 0px;height: 100%;background: #fff; box-shadow:0 0 16px #e2e7ea;}

</style>
<script>
  import HeaderPublc from '../public/set.vue'
  import cent from '../public/divesList.vue'
  export default {
    name: 'my-login',
    data () {
      return {
        fromData: {
          userName: null, // 账号
          password: null, // 密码
          clientType: 1 // 客户端类型
        },
        remember: false, // 是否记住密码
        error: false, // 登陆是否成功
        defaultLayers: null,
        ui: null,
        list: [],
        bubble: null,
        routeInstructionsContainer: null,
        input: null
      }
    },
    methods: {
      login () {
        this.$router.push({ name: 'map' })
      }
    },
    computed: {

    },
    mounted () {
      // 配置顶部菜单
      var parm = {logo: false, login: false, us: true, language: true, user: false, nav: false}
      this.$store.commit('updateNave', parm)
    },
    components: {
      HeaderPublc: HeaderPublc,
      cent: cent
    }
  }
</script>




